<style lang=less>
    @import '../../assets/index.less';
    @import './index.less';
</style>
<template>
    <div class="center">
        <p class="top-title">{{$t('center.index[0]')}}</p>
        <ul  class="router-list">
            <li :class="!item.url?'title':item.active?'active':''" v-for="(item,index) in routerList" :key="index">
                <router-link :to="item.url">{{item.name}}</router-link>
            </li>
        </ul> 
        <div class="center-right">
            <router-view/>
        </div>
    </div>

</template>
<script> 
import {login} from "../../api/user"
export default {
    data () {
        return {
            routerList:[//个人中心左侧列表路由 如果页面中存在跳转 则在上一层路由后加/ 例如/center/baseInfo/666  需要跟router.js中相对应 不可点击的url设置为空即可
                {name:this.$t('center.index[1]'), url:''},//个人信息
                {name:this.$t('center.index[2]'), url:'/center/baseInfo'},//基本信息
                {name:this.$t('center.index[3]'), url:'/center/identity'},//身份认证
                {name:this.$t('center.index[4]'), url:''},//我的资产
                {name:this.$t('center.index[5]'), url:'/center/assetInformation'},//资产信息
                // {name:this.$t('center.index[6]'), url:'/center/paymentMethod'},//支付方式
                {name:this.$t('center.index[7]'), url:'/center/capitalSubsidiary'},//资金明细
                {name:this.$t('center.index[8]'), url:''},//经纪人
                {name:this.$t('center.index[9]'), url:'/center/invitecode'},//我的邀请码
                {name:this.$t('center.index[10]'), url:'/center/friend'},//我的好友
                {name:this.$t('center.index[11]'), url:'/center/commission'},//推广佣金
            ]
        }
    },
    created () {
        this.setrouterListActive()
    },
    watch: {
        $route(){
            this.setrouterListActive()
        }
    },
    methods: {
        setrouterListActive(){
            this.routerList.forEach((item)=>{
                if(this.$route.path.indexOf(item.url) >= 0){
                    item.active = true
                } else{
                    item.active = false
                }
            });
        }
    }
}
</script>



